.box-out {
    z-index: -99;
    width: 720px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    top: 30px;
}

.box-inner {
    display: none;
    z-index: 99;
    width: 900px;
    /*display: flex;*/
    /* justify-content: space-between; */
    align-items: center;
    position: absolute;
    top: 350px;
    overflow: hidden;
    background-color: #fff;
}

.book {
    width: 180px;
    height: 255px;
    background-color: rgb(62, 71, 152);
    transition: all .3s ease-in-out;
    transform-origin: left center 0px;
    transform-style: preserve-3d;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    -webkit-backface-visibility: hidden;
    overflow: hidden;
}

.box-out .book::after {
    content: " ";
    display: block;
    opacity: 0;
    width: 180px;
    height: 255px;
    position: relative;
    left: 8px;
    transition: all .3s ease;
}

.box-out .book::before {
    content: " ";
    z-index: 999;
    display: block;
    width: 20px;
    height: 100px;
    opacity: 0;
    position: absolute;
    top: -12px;
    right: 8px;
    transition: all .25s;
    background: url('data:image/png;base64,');
    background-size: 16px;
    background-repeat: no-repeat;
}

